




































































import {Debounced} from '@/utils'
import {Vue, Component, Watch, Emit} from 'vue-property-decorator'

import public_config from '@/public.config'

interface search_item {
  q: string
}

@Component
export default class Search extends Vue {
  search_text: string = ''
  debounce_search_text: string = ''
  show_list: boolean = false
  search_items: search_item[] = []
  search_item_ind: string | number = ''
  search_item_old_ind: string | number = ''
  search_hover: boolean = false
  public_config: any = public_config

  get config() {
    return JSON.parse(this.$store.getters['user/config'])
  }

  // todo:获取搜索 icon 列表 id
  get search_list_personal() {
    if (this.config && this.config.front_config && this.config.front_config.search_list) {
      return this.config.front_config.search_list
    } else {
      return [1, 2, 3, 4, 5, 6, 7, 8, 9]
    }
  }

  // todo:计算 搜索 icon 列表
  get search_btn_list_personal() {
    let arr: object[] = []
    this.$store.state.search.search_list.forEach((item: any) => {
      if (this.search_list_personal.includes(item.id)) {
        arr.push(item)
      }
    })
    return arr
  }

  $axios: any;

  created(): void {
  }

  get list_length() {
    return this.search_items.length
  }

  private debounce_inp: Function = new Debounced().use(this.get_search_list, 500)
  private debounce_search_text_fn: Function = new Debounced().use(this.get_debounce_search_text, 500)

  @Watch('search_text')
  getNewSearchText(newVal: string) {
    this.change_parent_search_text()
    // if (newVal === '') {}
    this.show_list = true
    if (this.search_item_old_ind === this.search_item_ind) {
      this.debounce_inp()
      this.debounce_search_text_fn()
      this.search_item_ind = ''
      this.search_item_old_ind = ''
    }
  }

  @Emit('update:search_text_father')
  private change_parent_search_text() {
    return this.search_text;
  }

  @Watch('search_item_ind')
  getNewSearchItemInd(newVal: any, oldVal: any) {
    this.search_item_old_ind = oldVal
    if (newVal !== '') {
      let obj: any = this.search_items[newVal]
      if (!this.search_hover) {
        this.search_text = obj.q
      }
      // this.debounce_search_text_fn()
    }
  }

  calc(url: string) {
    if (url[0] === '/') url = this.public_config.BASE_URL + url
    return url
  }

  hide_list() {
    setTimeout(() => {
      this.show_list = false
    }, 150)
  }

  on_search_item_mousemove(i: number) {
    this.search_item_ind = i
    this.search_hover = true
  }

  get_debounce_search_text() {
    this.debounce_search_text = this.search_text
    this.inp_focus()
    // document.querySelectorAll('iframe').forEach(item => {
    //   console.log('item:', item)
    //   const iframe_win: any = item.contentWindow
    //   console.log('iframe_win:', iframe_win)
    //   const docu: any = iframe_win.document
    //   console.log('docu:', docu)
    //   docu.querySelector('html').style.background = "#333"
    // })
  }

  inp_focus() {
    const inp_element: any = this.$refs.search_inp
    inp_element.focus()
  }

  async get_search_list() {
    try {
      let res = await this.$axios.get(`/xz_server/main/getSearchList?wd=${this.search_text}`)
      this.search_items = res.data
    } catch (e) {
      console.log('e:', e)
    }
  }

  change_search_text(e: { target: { value: string; }; }) {
    console.log('e:', e)
    this.search_text = e.target.value
    this.show_list = true
    this.get_search_list()
    this.search_item_ind = ''
  }

  debounce(fn: any, wait = 1000, immediate: any) {
    let timer: NodeJS.Timeout | null = null
    return (args: any) => {
      // ??????????????this????????????
      if (timer) clearTimeout(timer)
      // ------ 新增部分 start ------
      // immediate 为 true 表示第一次触发后执行
      // timer 为空表示首次触发
      if (immediate && !timer) {
        fn.apply(this, args)
      }
      // ------ 新增部分 end ------
      timer = setTimeout(() => {
        fn.apply(this, args)
      }, wait)
    }
  }

  inp_on_keyup(e: { key: string; }) {
    // console.log('e:', e)
    let ind
    switch (e.key) {
      case 'ArrowUp':
        ind = Number(this.search_item_ind) - 1
        if (ind < 0) {
          this.search_item_ind = this.list_length + ind
        } else {
          this.search_item_ind = ind
        }
        break
      case 'ArrowDown':
        if (this.search_item_ind === '') {
          this.search_item_ind = 0
        } else {
          ind = Number(this.search_item_ind) + 1
          if (ind > this.list_length - 1) {
            this.search_item_ind = 0
          } else {
            this.search_item_ind = ind
          }
        }
        break
      case 'Enter':
        this.jump_default()
        break
    }
  }

  jump_default() {
    const config = JSON.parse(this.$store.getters['user/config'])
    const default_search_engine = config && config.front_config.default_search_engine || 1
    const item = this.$store.state.search.search_list.find((item: any) => item.id === default_search_engine)
    const jump_url_prefix = item.jump_url_prefix
    this.jump(jump_url_prefix)
  }

  jump(jump_url_prefix: string) {
    let search_text = this.search_hover ? this.search_items[this.search_item_ind as number].q : this.search_text
    window.open(`${jump_url_prefix}${search_text}`)
    this.show_list = false
    // this.search_text = ''
    this.search_item_ind = ''
  }
}
